.board__control {
	a {
		&:hover {
			text-decoration: underline; } }
	.board__control {
		&--item {
			padding-right: .5rem;
			padding-left: .5rem;
			.card {
				border-radius: 5px;
				border-top-right-radius: 35px;
				overflow: hidden;
				&--grey {
					box-shadow: inset 0 0 0 1px $grey; }
				&--blue {
					box-shadow: inset 0 0 0 1px $blue; }
				&--green {
					box-shadow: inset 0 0 0 1px $green; }
				.card__header {
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding: 1rem 1.5rem;
					&--grey {
						background: $grey; }
					&--blue {
						background: $blue; }
					&--green {
						background: $green; }
					.card__title {
						h2 {
							margin-bottom: 0;
							color: #ffffff;
							font-size: 1.5rem; } }
					.card__refresh {
						button {
							border: none;
							background: none;
							color: #ffffff;
							font-size: 1.3rem;
							&:focus {
								outline: none;
								cursor: pointer; } } } }
				.card__body {
					padding: .6rem;
					.c__block {
						min-height: 5rem; }
					.block__item {
						&:last-child {
							margin-bottom: 0; }
						&.sortable-chosen {
							&.block__item--low {
								box-shadow:  0px 0px 10px 0px #cfd8dc; }
							&.block__item--normal {
								box-shadow:  0px 0px 10px 0px #bbdefb; }
							&.block__item--medium {
								box-shadow:  0px 0px 10px 0px #b2dfdb; }
							&.block__item--danger {
								box-shadow:  0px 0px 10px 0px #ffcdd2; } } } } } }
		&--finish {
			.block__item {
				padding-right: 1.5rem;
				.block__action {
					.block__btn {
						&--moveTask {
							display: none; } } } } } } }
